import React from 'react';
import "./scss/Eduzone_programmes.scss";
import Diyads from "./Diya_ads";
import Data from "./data/programme.json";
import { Link } from "react-router-dom";
class Programmes extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ajax: false,
            channel: this.props.match.params.id,
            programme: this.props.match.params.pgm,
            data: Data,
            demo: false
        }
    }
    componentDidMount() {
        this.read_programmes()
    }
    async read_programmes() {
        await fetch(window.json_server + this.state.channel + "/programme/" + this.state.programme, {
            'cache': 'no-store'
        })
            .then(response => response.json())
            .then((jsonData) => {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
                if (jsonData.data != null) {
                    // jsonData is parsed json object received from url
                    this.setState({
                        data: jsonData.data,
                        ajax: true,
                        demo: jsonData.demo
                    })
                }
            })
            .catch((error) => {
                // handle your errors here
                console.log(error)
            });
    }
    render() {
        return (
            <div>
                <Diyads />
                <div>
                    <div className="date_slider">
                        <div className="row">
                            <div className="date_slide col-4 col-md-4 col-lg-4">
                                <button>Latest</button>
                            </div>
                            <div className="date_slide col-4 col-md-4  col-lg-4">
                                <button><i className="fa fa-arrows-h"></i></button>
                            </div>
                            <div className="date_slide col-4  col-md-4  col-lg-4">
                                <button>Previous <i className="fa fa-angle-double-right"></i></button>
                            </div>
                        </div>
                    </div>

                    <div className="programme_sheet">
                        {this.state.ajax === true ?
                            <div className="name_set">
                                {this.state.data.map(programme => {
                                    return (
                                        <a href="#a">
                                            <div className="item">{programme.name}</div>
                                        </a>)
                                })}
                            </div>
                            : null}
                        {this.state.ajax === true ?
                            <div className="programme_set">
                                {this.state.demo === true ?
                                    <div className="banner">

                                        {this.state.data.map(conent => {
                                            return (
                                                <div className="item">
                                                    {conent.playlist.filter(({ demo }) => demo === "True").map(playlist => {
                                                        return (
                                                                <div>
                                                                    <Link to={"/classroom/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + playlist.uri}>
                                                                        <div className="item-box">
                                                                            <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                            <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                        </div>
                                                                    </Link>
                                                                </div>
                                                        )
                                                    })}
                                                    {conent.playlist.filter(({ demo }) => demo === "False").map(playlist => {
                                                        return (
                                                                <div>
                                                                    <Link to={"/classroom/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + playlist.uri}>
                                                                        <div className="item-box demo">
                                                                            <i className="fa fa-lock"></i>
                                                                            <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                            <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                        </div>
                                                                    </Link>
                                                                </div>
                                                        )
                                                    })}
                                                </div>
                                            )
                                        })}

                                    </div> :
                                    <div className="banner">
                                        {this.state.data.map(conent => {
                                            return (
                                                <div className="item">
                                                    {conent.playlist.map(playlist => {
                                                        return (
                                                            <div>
                                                                <Link to={"/classroom/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + playlist.uri}>
                                                                    <div className="item-box">
                                                                        <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                        <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                    </div>
                                                                </Link>
                                                            </div>

                                                        )
                                                    })}

                                                </div>
                                            )
                                        })}
                                    </div>
                                }
                            </div>
                            : <img className="diya_load" alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" />}
                    </div>


                </div>
            </div>
        )
    }
}
export default Programmes;